import React from "react";
import GridList from "@material-ui/core/GridList";
import GridListTile from "@material-ui/core/GridListTile";
import { connect } from "react-redux";
import { Modal } from "semantic-ui-react";

import "./Work.css";
import { fetchSketchesData } from "../../actions";

class Work extends React.Component {
  state = { show: false, selectedTile: null };

  hideModal = () => {
    this.setState({ show: false, selectedTile: null });
  };

  componentDidMount() {
    this.props.fetchSketchesData();
  }

  expandAndDisplayImageOnClick = (tile) => {
    this.setState({ show: true, selectedTile: tile });
  };

  showViewWithModal = () => {
    return (
      <div className="root">
        <GridList
          cellHeight={"auto"}
          className="gridList"
          cols={6}
          spacing={20}
        >
          {this.props.sketchs.map((tile) => (
            <GridListTile key={tile.img} cols={tile.cols || 1}>
              <img
                className="art"
                src={tile.img}
                alt={tile.title}
                onClick={() => this.expandAndDisplayImageOnClick(tile)}
              />
            </GridListTile>
          ))}
        </GridList>

        <Modal
          open={this.state.show}
          onClose={this.hideModal}
          basic
          size="small"
          centered
        >
          <Modal.Content>
            <img
              className="portrait"
              src={this.state.selectedTile.img}
              alt={this.state.selectedTile.title}
            />
          </Modal.Content>
        </Modal>
      </div>
    );
  };

  showDefaultView = () => {
    return (
      <div className="root">
        <GridList cellHeight={400} className="gridList" cols={6} spacing={20}>
          {this.props.sketchs.map((tile) => (
            <GridListTile className="grid" key={tile.img} cols={tile.cols || 1}>
              <img
                className="art"
                src={tile.img}
                alt={tile.title}
                onClick={() => this.expandAndDisplayImageOnClick(tile)}
              />
            </GridListTile>
          ))}
        </GridList>
      </div>
    );
  };

  render() {
    const { show } = this.state;
    if (show) {
      return this.showViewWithModal();
    } else {
      return this.showDefaultView();
    }
  }
}

const mapStateToProps = (state) => {
  return { sketchs: state.sketchs };
};

export default connect(mapStateToProps, { fetchSketchesData })(Work);
